.display-card-container{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    width: 100%;
    margin-bottom: 10px;
}
.display-card-container .display-card img{
    width: 100%;
}
@media only screen and (min-width: 580px) {
    .display-card-container{
        width: 220px;
    }
    .display-card-container .display-card img{
        width: 220px;
    }
}



